import { connect } from "react-redux";
import {signIn} from '../../../store/actions/authActions';
import { Redirect } from "react-router-dom";
import React from 'react';
import sigInInterface from './signInInterface';

class userSignIn extends sigInInterface {
    
    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value

        });
    }
    handleSubmit = (e) => {
        e.preventDefault();
        console.log(this.state);
        this.props.signIn(this.state);
    }
    render() {
        const {authError, auth} = this.props;
        if(auth.uid) return <Redirect to='/'/>

        return (
            <div className="conatiner">
                <form onSubmit={this.handleSubmit} className="white">
                    <h5 className="grey-text text-darken-3">User Sign In</h5>
                    <div className="input-field">
                        <label htmlFor="email">Email</label>
                        <input type="email" id="email" onChange={this.handleChange} />
                    </div>
                    <div className="input-field">
                        <label htmlFor="password">Password</label>
                        <input type="password" id="password" onChange={this.handleChange} />
                    </div>
                    <div className="input-field">
                        <button className="btn pink lighten-1 z-depth-0">Login</button>
                        <div className="red-text center">
                            {authError ? <p>{authError}</p>: null}
                        </div>
                    </div>
                </form>
            </div>
        )
    }
}

const mapstateToProps = (state) => {
    return{
        authError: state.auth.authError,
        auth: state.firebase.auth
    }
}


const mapDispatchToProps = (dispatch) => {
    return{
signIn: (creds) => dispatch(signIn(creds))
    }
}

export default connect(mapstateToProps, mapDispatchToProps)(userSignIn);
